<template>
    <vue-tel-input :="bindProps" v-model.lazy="number"  :class="classForNumber" />
</template>

<script>
import { VueTelInput } from 'vue-tel-input'
export default {
components:{
    VueTelInput
},
props:{
   classForNumber:{
    type: String,
    default(){
        return ''
    }
   }
},
watch:{
    number(){
        this.$emit('phoneNumber', this.number)
    }
},
computed:{

},
data(){
    return {
        number:'',
        bindProps: {
            maxLen: 10,
            inputOptions: {
                showDialCode: true,
            },
            validCharactersOnly: true,
            defaultCountry: 'DE',
        },
    }
},
methods:{
 
}
}
</script>

